/*=============== GOOGLE FONTS ===============*/
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");

/*=============== VARIABLES CSS ===============*/
:root {
  /*--header-height:3rem;*/
  /*========== Colors ==========*/
  /*Color mode HSL(hue, saturation, lightness)*/
  --hue: 0;
  --sat: 0%;
  --title-color: hsl(var(--hue), var(--sat), 20%);
  --title-color-dark: hsl(var(--hue), var(--sat), 0%);
  --text-color: hsl(var(--hue), var(--sat), 46%);
  --body-color: hsl(var(--hue), var(--sat), 98%);
  --container-color: #ffffff;



  /*========== Font and typography ==========*/
  --body-font: "Poppins", sans-serif;

  /*.5rem = 8px | 1rem = 16px ...*/
  --big-font-size: 3.5rem;
  --h1-font-size: 2.25rem;
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
  --smaller-font-size: 0.813rem;
  --tiny-font-size: 0.625rem;

  /*========== Font weight ==========*/
  --font-normal: 400;
  --font-medium: 500;
  --font-semi-bold: 600;

  /*========== Margenes Bottom ==========*/
  /*.5rem = 8px | 1rem = 16px ...*/
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;

  /*========== z index ==========*/
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;

  --logo: rgba(0, 0, 0, 0.945);
  --dark: #313131;
  --dark1: #424242;
  --bgnew: linear-gradient(to top, #c4c5c7 0%, #dcdddf 52%, #ebebeb 100%);
}

body.light{
  --background-color: var(--body-color);
  --color-logo: var(--logo);
  --navlink: var(--title-color);
  --hoverlink: black;
  --navmenu: var(--body-color);
  --h1h2: var(--title-color);
  --bgbtn: var(--title-color);
  --txtbtn: rgba(211, 211, 211, 0.884);
  --colorhove: black;
  --btnborder: black;
  --btnhove: var(--body-color);
  --homeicon: var(--title-color);
  --hiconhove: var(--title-color-dark);
  --homescroll: var(--title-color);
  --arrowname: var(--title-color);
  --contact: none;
  --foter: var(--title-color);
  --footerhove: var(--title-color);
  --moon: url(./assets/moon.png);
  --close: var(--title-color);
  --contactcolor: var(--title-color);
  --bordercolor: rgba(0, 0, 0, 0.3);
  --svgdl: rgba(211, 211, 211, 0.884);
  --proj-card: var(--container-color);
  --card-title: var(--title-color);
  --proj-btn: var(--title-color);
  --proj-icon: black;
  --contact-card: var(--container-color);
  --contact-card-title: var(--title-color);
}

body.dark{
  --background-color: var(--dark);
  --color-logo: white;
  --navlink: white;
  --hoverlink: grey;
  --navmenu: var(--dark1);
  --h1h2: white;
  --bgbtn: white;
  --txtbtn: black;
  --colorhove: white;
  --btnborder: white;
  --homeicon: white;
  --hiconhove: grey;
  --homescroll: white;
  --arrowname: white;
  --contact: white;
  --footer: white;
  --footerhove: grey;
  --para: white;
  --span: white;
  --close: white;
  --contactcolor: whitesmoke;
  --bordercolor: white;
  --svgdl: black;
  --moon: url(./assets/sun.png);
  --proj-card: var(--dark1);
  --card-line: white;
  --card-title: white;
  --proj-btn: white;
  --contact-card: var(--dark1);
  --contact-card-title: white;
}

/* Responsive typography */
@media screen and (max-width: 992px) {
  :root {
    --big-font-size: 2.75rem;
    --h1-font-size: 1.5rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1rem;
    --normal-font-size: 0.938rem;
    --small-font-size: 0.813rem;
    --smaller-font-size: 0.75rem;
  }
}

/*=============== BASE ===============*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body,
button,
input,
textarea {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}

.header{
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999999;
  background-color: var(--background-color);
  transition: background-color 0.3s ease-in-out, color 0.6s ease-in-out;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  transition: background-color 0.3s ease-in-out, color 0.6s ease-in-out;
}

h1,
h2{
  color: var(--h1h2);
}

p, span{
  color: var(--para);
}

h3 {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
}

/*=============== REUSABLE CSS CLASSES ===============*/
.section {
  padding: 6rem 0 3rem;
}

.section-title {
  font-size: 3rem;
  color: var(--h1h2);
  margin-bottom: 2rem;
}

.section-title{
  text-align: center;
}

/*=============== LAYOUT ===============*/
.container {
  max-width: 968px;
  margin-left: auto;
  margin-right: auto;
}

.grid {
  display: grid;
  gap: 1.5rem;
}

/*=============== BUTTONS ===============*/
.button {
  display: inline-block;
  background-color: var(--bgbtn);
  color: var(--txtbtn);
  padding: 1.10rem 1.5rem;
  font-weight: bold;
  border: 1px solid var(--btnborder);

}

.button:hover {
  background-color: var(--btnhove);
  color: var(--colorhove);
}

.button-icon {
  margin-left: var(--mb-0-5);
}

.button--flex {
  display: inline-flex;
  align-items: center;
}

/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
  .container {
    margin-left: var(--mb-1-5);
    margin-right: var(--mb-1-5);
  }

  .button {
    padding: 1rem 1.75rem;
  }

  .button-icon {
    width: 22px;
    height: 22px;
  }
  
}

/* For medium devices */
@media screen and (max-width: 768px) {
  body {
    margin: 0 0 var(--header-height) 0;
  }

  .section {
    padding: 3rem 0 0;
  }
}

@media screen and (max-width: 576px) {
}

/* For small devices */
@media screen and (max-width: 350px) {
  :root {
    --big-font-size: 2.25rem;
  }

  .container {
    margin-left: var(--mb-1);
    margin-right: var(--mb-1);
  }
}
